	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import xuesheng from '@/views/xuesheng/list'
	import jiaoshi from '@/views/jiaoshi/list'
	import users from '@/views/users/list'
	import zuoyexinxi from '@/views/zuoyexinxi/list'
	import jiazhang from '@/views/jiazhang/list'
	import tongzhixinxi from '@/views/tongzhixinxi/list'
	import tongzhileixing from '@/views/tongzhileixing/list'
	import fankuijilu from '@/views/fankuijilu/list'
	import banjixinxi from '@/views/banjixinxi/list'
	import xueshengkaoqin from '@/views/xueshengkaoqin/list'
	import meiricaipu from '@/views/meiricaipu/list'
	import config from '@/views/config/list'
	import jiaoshipingjia from '@/views/jiaoshipingjia/list'
	import jiaoshiRegister from '@/views/jiaoshi/register'
	import jiaoshiCenter from '@/views/jiaoshi/center'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '首页',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '首页',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '修改密码',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/jiaoshiCenter',
			name: '教师个人中心',
			component: jiaoshiCenter
		}
		,{
			path: '/xuesheng',
			name: '学生',
			component: xuesheng
		}
		,{
			path: '/jiaoshi',
			name: '教师',
			component: jiaoshi
		}
		,{
			path: '/users',
			name: '管理员',
			component: users
		}
		,{
			path: '/zuoyexinxi',
			name: '作业信息',
			component: zuoyexinxi
		}
		,{
			path: '/jiazhang',
			name: '家长',
			component: jiazhang
		}
		,{
			path: '/tongzhixinxi',
			name: '通知信息',
			component: tongzhixinxi
		}
		,{
			path: '/tongzhileixing',
			name: '通知类型',
			component: tongzhileixing
		}
		,{
			path: '/fankuijilu',
			name: '反馈记录',
			component: fankuijilu
		}
		,{
			path: '/banjixinxi',
			name: '班级信息',
			component: banjixinxi
		}
		,{
			path: '/xueshengkaoqin',
			name: '学生考勤',
			component: xueshengkaoqin
		}
		,{
			path: '/meiricaipu',
			name: '每日菜谱',
			component: meiricaipu
		}
		,{
			path: '/config',
			name: '轮播图',
			component: config
		}
		,{
			path: '/jiaoshipingjia',
			name: '教师评价',
			component: jiaoshipingjia
		}
		]
	},
	{
		path: '/jiaoshiRegister',
		name: '教师注册',
		component: jiaoshiRegister
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
